"use client";
import React, { useState, useMemo } from "react";
import PageBreadcrumb from "@/components/common/PageBreadCrumb";
import { Table, TableBody, TableCell, TableHeader, TableRow } from "@/components/ui/table";
import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer } from "recharts";

interface Timesheet {
    id: number;
    date: string;
    regularHours: number;
    overtimeHours: number;
}

const initialTimesheets: Timesheet[] = [
    { id: 1, date: "2025-12-25", regularHours: 8, overtimeHours: 2 },
    { id: 2, date: "2025-12-26", regularHours: 7, overtimeHours: 1 },
    { id: 3, date: "2025-12-27", regularHours: 8, overtimeHours: 0 },
    { id: 4, date: "2025-12-28", regularHours: 6, overtimeHours: 3 },
    { id: 5, date: "2025-12-29", regularHours: 8, overtimeHours: 1 },
    { id: 6, date: "2025-12-30", regularHours: 7, overtimeHours: 2 },
    { id: 7, date: "2025-12-31", regularHours: 8, overtimeHours: 1 },
];

export default function TimesheetPage() {
    const [timesheets, setTimesheets] = useState<Timesheet[]>(initialTimesheets);
    const [date, setDate] = useState("");
    const [regularHours, setRegularHours] = useState(8);
    const [overtimeHours, setOvertimeHours] = useState(0);
    const [currentPage, setCurrentPage] = useState(1);
    const [editingTimesheet, setEditingTimesheet] = useState<Timesheet | null>(null);

    const timesheetsPerPage = 5;
    const totalPages = Math.ceil(timesheets.length / timesheetsPerPage);
    const displayedTimesheets = timesheets.slice(
        (currentPage - 1) * timesheetsPerPage,
        currentPage * timesheetsPerPage
    );

    const handleAddOrUpdateTimesheet = () => {
        if (!date) return;
        if (editingTimesheet) {
            setTimesheets(timesheets.map(t =>
                t.id === editingTimesheet.id
                    ? { ...t, date, regularHours, overtimeHours }
                    : t
            ));
            setEditingTimesheet(null);
        } else {
            const newTimesheet: Timesheet = {
                id: timesheets.length + 1,
                date,
                regularHours,
                overtimeHours,
            };
            setTimesheets([newTimesheet, ...timesheets]);
        }
        setDate("");
        setRegularHours(8);
        setOvertimeHours(0);
    };

    const handleEdit = (t: Timesheet) => {
        setEditingTimesheet(t);
        setDate(t.date);
        setRegularHours(t.regularHours);
        setOvertimeHours(t.overtimeHours);
    };

    const handleDelete = (id: number) => {
        setTimesheets(timesheets.filter(t => t.id !== id));
    };

    const totalHours = useMemo(() => {
        return timesheets.reduce((sum, t) => sum + t.regularHours + t.overtimeHours, 0);
    }, [timesheets]);

    const averageHours = useMemo(() => {
        if (!timesheets.length) return 0;
        return totalHours / timesheets.length;
    }, [totalHours, timesheets.length]);

    // Prepare weekly chart data (Mon-Sun)
    const chartData = useMemo(() => {
        const weekDays = ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"];
        return weekDays.map((day, idx) => {
            const ts = timesheets.find(t => {
                const d = new Date(t.date);
                return d.getDay() === idx;
            });
            return {
                day,
                regular: ts ? ts.regularHours : 0,
                overtime: ts ? ts.overtimeHours : 0,
            };
        });
    }, [timesheets]);

    return (
        <div className="p-6">
            <PageBreadcrumb pageTitle="Timesheet" />

            {/* Add/Edit Timesheet */}
            <div className="mb-6 rounded-xl border border-gray-200 bg-white p-6 shadow space-y-4">
                <h2 className="text-lg font-semibold text-gray-800 dark:text-white">
                    {editingTimesheet ? "Edit Timesheet" : "Add Timesheet"}
                </h2>
                <div className="grid grid-cols-1 sm:grid-cols-4 gap-4 items-end">
                    <div>
                        <label className="block text-sm font-medium mb-1">Date</label>
                        <input
                            type="date"
                            className="w-full border rounded px-3 py-2"
                            value={date}
                            onChange={(e) => setDate(e.target.value)}
                        />
                    </div>
                    <div>
                        <label className="block text-sm font-medium mb-1">Regular Hours</label>
                        <input
                            type="number"
                            className="w-full border rounded px-3 py-2"
                            value={regularHours}
                            onChange={(e) => setRegularHours(Number(e.target.value))}
                        />
                    </div>
                    <div>
                        <label className="block text-sm font-medium mb-1">Overtime Hours</label>
                        <input
                            type="number"
                            className="w-full border rounded px-3 py-2"
                            value={overtimeHours}
                            onChange={(e) => setOvertimeHours(Number(e.target.value))}
                        />
                    </div>
                    <button
                        onClick={handleAddOrUpdateTimesheet}
                        className="bg-blue-600 text-white px-4 py-2 rounded hover:bg-blue-700 transition"
                    >
                        {editingTimesheet ? "Update" : "Add"}
                    </button>
                </div>
            </div>

            {/* Summary */}
            <div className="mb-6 grid grid-cols-1 sm:grid-cols-2 gap-4">
                <div className="p-4 border rounded-xl bg-white shadow">
                    <h3 className="text-sm font-medium text-gray-500">Average Hours/Day</h3>
                    <p className="text-2xl font-bold">{averageHours.toFixed(2)} hrs</p>
                </div>
                <div className="p-4 border rounded-xl bg-white shadow">
                    <h3 className="text-sm font-medium text-gray-500">Total Overtime</h3>
                    <p className="text-2xl font-bold">
                        {timesheets.reduce((sum, t) => sum + t.overtimeHours, 0)} hrs
                    </p>
                </div>
            </div>

            {/* Weekly Chart with Overtime Highlight */}
            <div className="mb-6 p-4 border rounded-xl bg-white shadow h-64">
                <h3 className="text-lg font-semibold mb-2 text-gray-800 dark:text-white">This Week Hours</h3>
                <ResponsiveContainer width="100%" height="100%">
                    <BarChart data={chartData} margin={{ top: 20, right: 30, left: 0, bottom: 5 }}>
                        <CartesianGrid strokeDasharray="3 3" />
                        <XAxis dataKey="day" />
                        <YAxis />
                        <Tooltip />
                        <Bar dataKey="regular" stackId="a" fill="#3b82f6" />
                        <Bar dataKey="overtime" stackId="a" fill="#f97316" />
                    </BarChart>
                </ResponsiveContainer>
            </div>

            {/* Timesheet Table */}
            <div className="overflow-hidden rounded-xl border border-gray-200 bg-white dark:border-white/[0.05] dark:bg-white/[0.03] p-6">
                <div className="max-w-full overflow-x-auto">
                    <Table>
                        <TableHeader className="bg-gray-50 dark:bg-gray-800">
                            <TableRow>
                                <TableCell isHeader>Date</TableCell>
                                <TableCell isHeader className="text-right">Regular Hours</TableCell>
                                <TableCell isHeader className="text-right">Overtime Hours</TableCell>
                                <TableCell isHeader className="text-right">Total Hours</TableCell>
                                <TableCell isHeader className="text-center">Actions</TableCell>
                            </TableRow>
                        </TableHeader>
                        <TableBody className="divide-y divide-gray-100 dark:divide-white/[0.05]">
                            {displayedTimesheets.map((t) => (
                                <TableRow key={t.id}>
                                    <TableCell>{t.date}</TableCell>
                                    <TableCell className="text-right">{t.regularHours}</TableCell>
                                    <TableCell className="text-right">{t.overtimeHours}</TableCell>
                                    <TableCell className="text-right">{t.regularHours + t.overtimeHours}</TableCell>
                                    <TableCell className="text-center flex justify-center gap-2">
                                        <button
                                            className="px-2 py-1 bg-yellow-400 text-white rounded"
                                            onClick={() => handleEdit(t)}
                                        >
                                            Edit
                                        </button>
                                        <button
                                            className="px-2 py-1 bg-red-600 text-white rounded"
                                            onClick={() => handleDelete(t.id)}
                                        >
                                            Delete
                                        </button>
                                    </TableCell>
                                </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                </div>

                {/* Pagination */}
                {totalPages > 1 && (
                    <div className="mt-4 flex justify-center gap-2">
                        <button
                            onClick={() => setCurrentPage(Math.max(currentPage - 1, 1))}
                            className="px-3 py-1 border rounded"
                            disabled={currentPage === 1}
                        >
                            Prev
                        </button>
                        {[...Array(totalPages)].map((_, idx) => (
                            <button
                                key={idx}
                                onClick={() => setCurrentPage(idx + 1)}
                                className={`px-3 py-1 border rounded ${currentPage === idx + 1 ? "bg-blue-600 text-white" : ""}`}
                            >
                                {idx + 1}
                            </button>
                        ))}
                        <button
                            onClick={() => setCurrentPage(Math.min(currentPage + 1, totalPages))}
                            className="px-3 py-1 border rounded"
                            disabled={currentPage === totalPages}
                        >
                            Next
                        </button>
                    </div>
                )}
            </div>
        </div>
    );
}
